* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;

}

.country-item {
  display: flex;
}

.country-flag {
  width: 70px;
  height: 50px;
  margin: 20px;
}

.mini-container {
  display: flex;
  align-items: center;
}

.country-flg {
  width: 120px;
  height: 80px;
  margin: 30px;
}

.country-name {
  font-size: 60px;
  font-weight: 700;
}

.title-info {
  font-size: 25px;
  font-weight: 600;
}

.info {
  font-size: 20px;
  font-weight: 400;
}